import React from 'react'
import { useState } from 'react';
import './Header.css'
import { Link } from "react-router-dom";
import { NavLink } from 'react-router-dom';
import logo from "../../PublicBar/Images/mainLogo.png"
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import FaceBook from '../../PublicBar/Images/Facebook.png'
import Instagram from '../../PublicBar/Images/Instagram.png'
import LinkedIn from '../../PublicBar/Images/LinkedIn.png'
import Twitter from '../../PublicBar/Images/Twitter.png'
const routes = [
  
 
  {
    path: "/browse-projects",
    name: "Projects",
    id: "projects"
  },
  {
    path: "/hire-freelancer",
    name: "Hire Freelancer",
    id: "hire-freelancer"
  },
  {
    path: "/blogs",
    name: "Blogs",
    id: "blogs"
  },

  // {
  //   path: "/login",
  //   name: "Login",
  //   id: "login"
  // },
  // {
  //   path: "/sign-up",
  //   name: "Sign Up",
  //   id: "sign-up"
  // },

]

function Header({ children }) {
  return (
    <>
      <div className="Public_Header">
        <nav className='Navbar '>
          <div className="sub_header">
            <div className='SubHeaderRow container'>
              <div>
                <div className='btn-text'>
                  <i class="fa-light fa-envelope"></i>
                  info@hirestrom.com
                </div>
              </div>
              <div className='SubHeaderCol2'>
              <div className='icon-btn-text'>
              <a href="https://www.instagram.com/" target="_blank"><i class="fa-brands fa-instagram"></i></a>
              <a href="#" target="_blank"><i class="fa-brands fa-square-facebook"></i></a>
              <a href="#" target="_blank"><i class="fa-brands fa-linkedin"></i></a>
              <a href="#" target="_blank"><i class="fa-sharp fa-solid fa-basketball"></i></a>
            </div>
              </div>
              <div className='SubHeader-LG-Sig'>
                <ul>
                <Link to="/login" className=''>
                  <li>
                    <button className='Login-btn'>Login</button>
                  </li>
                </Link>
                <li className='vl'></li>
                <Link to="/sign-up">
                  <li>
                    <button className='Sign-up-btn'>Sign Up</button>
                  </li>
                </Link>
                </ul>
              </div>
            </div>
          </div>
          <div className="P_Header_block container">

            <div className='Logo'>
              <Link to="/">
              <img src={logo} alt="" />
              </Link>
            </div>
            <div className='Menu_List'>
              <ul className='Menu_Ul_List'>
                {routes.map((route) => (
                  <li>
                    <NavLink to={route.path} key={route.name} className="link_is_" id={route.id} >
                      {route.name}
                    </NavLink>
                  </li>
                ))}
                <Link to="/post-project">
                <li>
                    <button className='Sign-up-btn ' ><a href="">Post a project</a></button>
                  </li>
                  </Link>
              </ul>
            </div>

          </div>
        </nav>
      </div>

    </>
  )
}

export default Header