import './App.css';
import Header from './Components/Header/Header';
import { BrowserRouter as Router, Routes, Route, useParams, unstable_HistoryRouter } from 'react-router-dom';
import Login from './Components/Login/Login';
import Home from './PublicBar/Pages/HomePage/Home';
import Project from './PublicBar/Pages/ProjectPage/Project';
import HireFreelancer from './PublicBar/Pages/HireFreelancerPage/HireFreelancer';
import Blog from './PublicBar/Pages/BlogPage/Blog';

import SignUp from './Components/SignUp/SignUp';
import PostProject from './Components/PostProject/PostProject';
import Footer from './Components/Footer/Footer';

function App() {
  return (
    <div className="App ">
      <div className='Main-Wrapper'>
      <Router>
        <Header/>
        <Routes>
          <Route path="/"
            element={
              <Home/>
            }
          />
          <Route path="/browse-projects"
            element={
              <Project />
            }
          />
          <Route path="/hire-freelancer"
            element={
              <HireFreelancer />
            }
          />
          <Route path="/blogs"
            element={
              <Blog />
            }
          />
          <Route path="/post-project"
            element={
              <PostProject/>
            }
          />
        </Routes>
        <Routes>
          <Route path="/login" element={<Login />} />
        </Routes>
        <Routes>
          <Route path="/sign-up" element={<SignUp />} />
        </Routes>
        <Footer/>
      </Router>
      </div>
    </div>
  );
}

export default App;
